<template>
  <div class="home page-container">
    <div
      class="banner overflow-hidden"
      :style="{ backgroundPosition: `${offsetX}px ${offsetY}px` }"
    ></div>
    <div
      class="page-head d-flex flex-direction-column align-items-center justify-content-center"
    >
      <p class="mt-0 mb-4 fw-7 fs-6">I'm Tian Chen</p>
      <p class="mt-0 mb-8 fw-1 fs-1">Full Stack Development Engineer</p>
      <div class="mt-4 d-flex justify-content-between">
        <a href="mailto:tian.chen1127@outlook.com" target="_blank"
          ><img class="mr-8 social-icon" :src="getBg('mail.svg')"
        /></a>
        <a href="https://github.com/TheT92" target="_blank"
          ><img class="mr-8 social-icon" :src="getBg('GithubFilled.svg')"
        /></a>
        <a
          href="https://www.linkedin.com/in/tian-chen-233037326/"
          target="_blank"
          ><img class="mr-8 social-icon" :src="getBg('LinkedinFilled.svg')"
        /></a>
        <a
          href="https://drive.google.com/file/d/1YWCrff97ql5ufLZ_K0Qeg2krduGEuqCd/view"
          target="_blank"
          ><img class="social-icon" :src="getBg('cv.svg')"
        /></a>
      </div>
    </div>

    <h4 class="d-flex justify-content-center text-white tabs">
      <span
        @click="toggleActive(1)"
        class="tab-item"
        :class="{ active: activeTab == 1 }"
        >About Me</span
      >
      <span class="ml-6 mr-6 divider"></span>
      <span
        @click="toggleActive(2)"
        class="tab-item"
        :class="{ active: activeTab == 2 }"
        >My Projects</span
      >
    </h4>
    <div class="overflow-hidden full-width content-card">
      <div class="d-flex detail-card" :class="{ transform: activeTab == 2 }">
        <div id="introduction" class="text-white flex-1">
          <div class="introduction-inner">
            <div class="pre-wrap shadowed-text" v-html="INTRODUCTION"></div>
            <div class="d-flex mt-8 detail-info">
              <div class="flex-1 mr-6">
                <h4 class="shadowed-text">My Advantages</h4>
                <p>
                  <ul>
                    <li class="mb-4 shadowed-text" v-for="(v, i) in ADVANTAGES" :key="i" v-html="v"></li>
                  </ul>
                </p>
                <h4 class="shadowed-text">My Tech Stack</h4>
                <span
                  v-for="(v, i) in TECHSTACKS"
                  :key="i"
                  class="keyword fs-0"
                  :style="{ backgroundColor: getColor(v) }"
                >
                  {{ v }}
                </span>
              </div>
              <div class="timeline flex-2">
                <h4 class="shadowed-text">Timeline</h4>
                <div class="event-item d-flex">
                  <div class="timeline-item active mr-8">NOW</div>
                  <div class="flex-1 d-flex align-items-center shadowed-text">
                    A new chapter has begun...
                  </div>
                </div>
                <div class="event-item d-flex">
                  <div class="timeline-item mr-8">2025</div>
                  <div class="flex-1 d-flex align-items-center shadowed-text">
                    Finished my Master's Degree of Science in Computing in
                    September.
                  </div>
                </div>
                <div class="event-item d-flex">
                  <div class="timeline-item mr-8">2023</div>
                  <div class="flex-1 d-flex align-items-center shadowed-text">
                    Took a career break to pursue advanced academic
                    qualifications in Ireland, completing both a Higher Diploma
                    and a Master’s degree in Computing to deepen my expertise
                    and enhance my professional potential.
                  </div>
                </div>
                <div class="event-item d-flex">
                  <div class="timeline-item mr-8">2023</div>
                  <div class="flex-1 d-flex align-items-center shadowed-text">
                    Left the last company, filled with trepidation and
                    anticipation for the future, I arrived in Ireland—this is
                    where the story begins.
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div id="projects" class="content-card mb-0 flex-1 clear-float pt-6">
          <div
            class="project-item-wrap float-left mb-4"
            v-for="(item, index) in MY_PROJECTS"
            :key="index"
          >
            <div class="project-item blur-card p-4 overflow-hidden">
              <router-link
                :to="item.link"
                class="text-decoration-none text-white"
              >
                <h3 class="mt-0 mb-4 fs-1">&lt; {{ item.name }} &gt;</h3>

                <span
                  class="d-inline-block item-desc ellipsis overflow-hidden mb-4"
                  :alt="item.desc"
                  >{{ item.desc }}</span
                >
                <div class="tags">
                  <span
                    v-for="(v, i) in item.keywords"
                    :key="i"
                    class="keyword fs-0"
                    :style="{ backgroundColor: getColor(v) }"
                  >
                    {{ v }}
                  </span>
                </div>
                <div class="cover-wrap overflow-hidden">
                  <div
                    class="cover-img"
                    :style="{ backgroundImage: `url(${getBg(item.img)})` }"
                  ></div>
                </div>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>

    <p class="footer mb-6 fs-0 w-100 text-white text-align-center">
      &copy; 2025 Tian Chen. All rights reserved.
    </p>
  </div>
</template>
  
  <script>
import { INTRODUCTION, MY_PROJECTS, TECHSTACKS, ADVANTAGES } from "@/constant";
export default {
  name: "HomePage",
  components: {},
  data() {
    return {
      INTRODUCTION,
      TECHSTACKS,
      ADVANTAGES,
      MY_PROJECTS,
      windowWidth: 0,
      windowHeight: 0,
      offsetX: 0,
      offsetY: 0,
      activeIndex: 0,
      activeTab: 1,
    };
  },
  computed: {
    getBg() {
      return function (url) {
        return require(`@/assets/${url}`);
      };
    },
    getColor() {
      return function (keyword) {
        if (keyword == "Java") {
          return "#ef8d07";
        } else if (keyword == "Spring") {
          return "#6db33f";
        } else if (keyword == "Vue") {
          return "#42b983";
        } else if (keyword == "ElementUI") {
          return "#409eff";
        } else if (keyword == "PostgreSQL") {
          return "#336791";
        } else if (keyword == "PHP") {
          return "#7a86b8";
        } else if (keyword == "Bootstrap") {
          return "#712cf9";
        } else if (keyword == "MySQL") {
          return "#3E6E93";
        } else if (keyword == "HTML") {
          return "#e54c21";
        } else if (keyword == "Unity") {
          return "#000000";
        } else if (keyword == "C++") {
          return "#048";
        } else if (keyword == "git") {
          return "#f54d27";
        } else if (keyword == "React") {
          return "#58c4dc";
        } else if (keyword == "React Native") {
          return "#58c4dc";
        } else if (keyword == "Python") {
          return "#2d618c";
        } else if (keyword == "FastAPI") {
          return "#00ccb8";
        } else if (keyword == "JavaScript") {
          return "#857c36";
        } else if (keyword == "Redis") {
          return "#ff4438";
        } else if(keyword == 'WXSS' || keyword == 'WXML') {
          return '#07c160';
        } else {
          return "#1890ff";
        }
      };
    },
  },
  methods: {
    toggleActive(index) {
      this.activeTab = index;
    },
    onHover(index) {
      this.activeIndex = index;
    },
    updateMousePosition(event) {
      let x = Math.ceil((event.clientX - this.windowWidth / 2) / 20);
      let y = Math.ceil((event.clientY - this.windowHeight / 2) / 20);
      const banner = document.querySelector(".banner");
      const rect = banner.getBoundingClientRect();
      if (event.clientY >= rect.top && event.clientY <= rect.bottom) {
        x = x > 20 ? 20 : x < -20 ? -20 : x;
        y = y > 20 ? 20 : y < -20 ? -20 : y;
      } else {
        x = 0;
        y = 0;
      }
      this.offsetX = 0 - x;
      this.offsetY = 0 - y;
    },
  },
  activated() {
    this.windowWidth = document.documentElement.clientWidth;
    this.windowHeight = document.documentElement.clientHeight;
    window.addEventListener("mousemove", this.updateMousePosition);
  },
  deactivated() {
    window.removeEventListener("mousemove", this.updateMousePosition);
  },
  beforeDestroy() {
    window.removeEventListener("mousemove", this.updateMousePosition);
  },
};
</script>
  
  <style scoped lang="scss">
@import "../style/home.scss";
</style>
  